footer .container {
  max-width: 1170px;
}
footer nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
footer .logo {
  height: 28px;
  margin-bottom: 78px;
}
footer .logo img {
  height: 28px;
  width: 109px;
}
footer .join-news-letter {
  display: flex;
  align-items: flex-start;
}
@media (max-width: 425px) {
  footer .join-news-letter {
    margin-top: 41px;
  }
}
footer .join-news-letter p {
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  color: #ebf4f1;
  margin-bottom: 10px;
  margin-top: unset;
  text-align: left;
}
footer .join-news-letter img {
  height: 125px;
}
footer .input {
  margin-top: -25px;
  max-width: 460px;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
}
footer .input input {
  background: #1c2126;
  border: 1px solid #262f37;
  box-sizing: border-box;
  border-radius: 8px;
  height: 56px;
  width: 100%;
  padding-left: 40px;
  font-size: 16px;
  line-height: 180%;
  color: #fff;
}
footer .input input::placeholder {
  color: #ebf4f1;
}
footer .input input:focus {
  outline: none;
  box-shadow: none;
  border: 1px solid #477db3;
  transition: 0.3 ease-in-out;
}
footer .input .prepend-icon {
  height: 12px;
  width: 16px;
  margin-right: -30px;
  z-index: 11;
}
footer .input button {
  margin-left: -50px;
}
footer p.copyright {
  margin-top: 24px;
}
@media (max-width: 425px) {
  footer p.copyright {
    text-align: left;
  }
}
footer .newsletter {
  display: unset;
  width: 100%;
  padding: 0;
  margin-top: 41px;
}
@media (min-width: 880px) {
  footer .newsletter {
    max-width: 430px;
  }
}
footer .newsletter h5,
footer .newsletter p {
  text-align: left;
}
footer .newsletter > div {
  display: flex;
  justify-content: space-between;
  margin: 0;
  width: 100%;
  max-width: unset;
  align-items: center;
}
footer .newsletter > div div {
  order: 1;
  max-width: unset;
  margin: unset;
}
footer .newsletter > div img {
  order: 2;
  width: 125px;
}
footer .newsletter form {
  background: #1c2126;
  border: 1px solid #262f37;
  box-sizing: border-box;
  border-radius: 8px;
  margin-top: -10px;
}
footer .newsletter input {
  color: #fff;
}
footer .newsletter input::placeholder {
  color: #ebf4f1;
  opacity: 1;
}